import React from 'react'
import { NavLink } from 'react-router-dom'
import './NavMobile.css'
import { useSelector } from 'react-redux'

const NavMobile = () => {
  const [isClicked, setIsClicked] = React.useState(false)
  const toggleSwitch = () => isClicked ? setIsClicked(false) : setIsClicked(true)

  const { navList, isOpen } = useSelector(({ nav }) => ({
    navList: nav.list,
    isOpen: nav.isOpen
  }))

  const btn = isClicked ? "menu_btn invert" : "menu_btn"
  return (
      <div className="nav_mobile_container">
        {
          navList.map(i => (
            <div className="nav_btn_wrapper" key={i.id}>
              <NavLink to={i.navlink} >
                <img src={i.src} alt="menu" className={btn} />
              </NavLink>
            </div>
          ))
        }
      </div>
  )
}

export default NavMobile
